@import "components/header";
@import "components/asside";
@import "components/footer";

body{
  .content{
    margin: 25px 0 35px;
    .container{
      .row{
        .col-lg-3{
          .aside{
            &__bestHolo,
            &__newHolo,
            &__threeD,
            &__examples,
            &__custom{
              display: none;
            }
          }
        }
        .col-lg-9{
          .shoppingCart{
            &__headline{
              margin: 35px 0 15px 15px;
              h5{
                font: 1.25em/1.5em "Arial";
                font-weight: bold;
                text-transform: uppercase;
              }
            }
            &__ID{
              margin: 15px 0 25px 15px;
              p{
                font: .875em/1em "Arial";
                font-weight: bold;
                color: #5c5c5c;
              }
            }
            &__items{
              div{
                padding: 10px 0;
                border-top: 1px solid #c7c7c7;
                border-bottom: 1px solid #c7c7c7;
                ul{
                  display: flex;
                  justify-content: space-around;
                  align-items: center;
                  li{
                    img{
                      width: 90px;
                      height: 90px;
                    }
                  }
                  .item__info{
                    width: 25%;
                    .itemName{
                      font: .875em/1.25em "Arial";
                      font-weight: bold;
                      text-transform: uppercase;
                    }
                    .itemSize{
                      font: .75em/1.25em "Arial";
                      color: #525252;
                    }
                  }
                  .quantity{
                    display: flex;
                    align-items: center;
                    p{
                      margin: 0 6px;
                      font: .875em/1em "Arial";
                      color: #525252;
                    }
                    input{
                      margin: 0 6px;
                      padding: 0 0 0 10px;
                      width: 70px;
                      height: 30px;
                      font: 1em/30px "Arial";
                      color: #525252;
                      border: 1px solid #c7c7c7;
                      outline: none;
                    }
                    span{
                      margin: 0 6px;
                      display: inline-block;
                      width: 30px;
                      height: 30px;
                      text-align: center;
                      background-color: #eee;
                      border: 1px solid #c7c7c7;
                      i{
                        font-size: 20px;
                        line-height: 30px;
                      }
                      .refresh{
                        color: #8d8d8d;
                      }
                      .cross{
                        color: #ff5858;
                      }
                      &:hover{
                        cursor: pointer;
                      }
                    }
                  }
                  .price{
                    p{
                      font: .875em/1em "Arial";
                      color: #525252;
                    }
                    .amount{
                      span{
                        font: 1.063em/1.188em "Arial";
                        font-weight: bold;
                        color: #0081ef;
                      }
                    }
                  }
                }
              }
            }
            &__discount{
              margin: 50px 0 25px;
              &__headline{
                margin: 0 0 15px;
                font: .875em/1em "Arial";
                color: #525252;
              }
              &__coupon,
              &__certificate,
              &__deliveryCost{
                margin: 5px 0;
                border: 1px solid #ddd;
                h6{
                  padding: 0 0 0 15px;
                  font: 1em/40px "Arial";
                  color: #333;
                  background-color: #f5f5f5;
                  border-bottom: 1px solid #ddd;
                  &:hover{
                    cursor: pointer;
                  }
                }
                ul{
                  margin: 15px 0;
                  display: flex;
                  justify-content: space-around;
                  align-items: center;
                  li{
                    display: flex;
                    align-items: center;
                    p{
                      font: .875em/1em "Arial";
                      color: #525252;
                    }
                    input{
                      padding: 15px;
                      width: 470px;
                      height: 35px;
                      font: .875em/1em "Arial";
                      color: #525252;
                      border-top: 1px solid #ccc;
                      border-right-style: none;
                      border-bottom: 1px solid #ccc;
                      border-left: 1px solid #ccc;
                      outline: none;
                    }
                    span{
                      width: 130px;
                      height: 35px;
                      display: inline-block;
                      font: 1em/35px "Arial";
                      color: #fff;
                      text-align: center;
                      background-color: #9e9e9e;
                      border: 1px solid #9e9e9e;
                      &:hover{
                        cursor: pointer;
                      }
                    }
                  }
                }
              }
              &__continue{
                margin: 15px 0 0;
                display: flex;
                justify-content: flex-end;
                span{
                  width: 205px;
                  height: 35px;
                  display: inline-block;
                  font: 1em/35px "Arial";
                  color: #fff;
                  text-align: center;
                  background-color: #9e9e9e;
                  border: 1px solid #9e9e9e;
                  &:hover{
                    cursor: pointer;
                  }
                }
              }
            }
            &__securePayment{
              &__headline{
                h6{
                  font: 1.25em/1.5em "Arial";
                  font-weight: bold;
                  text-transform: uppercase;
                }
                p{
                  font: .875em/1.5em "Arial";
                  color: #525252;
                  b{
                    font-size: 1.75em;
                    color: #ff5a5a;
                  }
                }
                form{
                  ul{
                    width: 85%;
                    li{
                      margin: 10px 0;
                      display: flex;
                      justify-content: space-between;
                      align-items: center;
                      p{
                        font: .875em/1.25em "Arial";
                        color: #525252;
                      }
                      .important{
                        font-weight: bold;
                      }
                      label{
                        width: 405px;
                        display: flex;
                        span{
                          width: 5%;
                          font-size: 1.75em;
                          line-height: 25px;
                          color: #ff5a5a;
                        }
                        input{
                          padding: 0 0 0 5px;
                          width: 95%;
                          height: 25px;
                          font: .875em/25px "Arial";
                          color: #525252;
                          border: 1px solid #c7c7c7;
                          outline: none;
                        }
                        .error{
                          border-color: #e06565;
                        }
                        ::-webkit-input-placeholder{
                          color: transparent;
                        }
                        ::-moz-placeholder{
                          color: transparent;
                        }
                        :-moz-placeholder{
                          color: transparent;
                        }
                        :-ms-input-placeholder{
                          color: transparent;
                        }
                      }
                    }
                    .cn{
                      label{
                        span{
                          opacity: 0;
                        }
                      }
                    }
                    .state{
                      .important{
                        span{
                          font-weight: normal;
                        }
                      }
                      label{
                        align-items: center;
                        position: relative;
                        span{
                          opacity: 0;
                        }
                        select{
                          padding: 0 0 0 5px;
                          width: 50px;
                          height: 25px;
                          font: .875em/25px "Arial";
                          color: #525252;
                          border: 1px solid #c7c7c7;
                          outline: none;
                          -webkit-appearance: none;
                          -moz-appearance: none;
                          -ms-appearance: none;
                          appearance: none!important;
                        }
                        select::-ms-expand{
                          display: none;
                        }
                        i{
                          position: absolute;
                          top: 6px;
                          left: 42px;
                        }
                        p{
                          margin: 0 10px;
                          white-space: nowrap;
                        }
                        input{
                          width: 60%;
                        }
                      }
                    }
                    .country{
                      label{
                        position: relative;
                        select{
                          padding: 0 0 0 5px;
                          width: 95%;
                          height: 25px;
                          font: .875em/25px "Arial";
                          color: #525252;
                          border: 1px solid #c7c7c7;
                          outline: none;
                          -webkit-appearance: none;
                          -moz-appearance: none;
                          -ms-appearance: none;
                          appearance: none!important;
                        }
                        select::-ms-expand{
                          display: none;
                        }
                        i{
                          position: absolute;
                          top: 6px;
                          right: 7px;
                        }
                      }
                    }
                    .fax{
                      label{
                        span{
                          opacity: 0;
                        }
                      }
                    }
                    .notes{
                      align-items: normal;
                      label{
                        span{
                          opacity: 0;
                        }
                        textarea{
                          padding: 5px;
                          width: 95%;
                          height: 120px;
                          font: .875em/25px "Arial";
                          color: #525252;
                          border: 1px solid #c7c7c7;
                          resize: none;
                          outline: none;
                        }
                      }
                    }
                    .selectPayment{
                      align-items: normal;
                      .important{
                        margin: 15px 0 0;
                        span{
                          width: 5%;
                          display: none;
                          font-size: 1.75em;
                          line-height: 25px;
                          color: #ff5a5a;
                        }
                      }
                      label{
                        flex-wrap: wrap;
                        span{
                          display: inline-block;
                          margin: 15px 0 0;
                        }
                        ul{
                          margin: 0 0 0 15px;
                          width: unset;
                          li{
                            justify-content: flex-start;
                            input{
                              width: 15px;
                              height: 15px;
                              &:hover{
                                cursor: pointer;
                              }
                            }
                            img{
                              margin: 0 15px;
                              width: 55px;
                              height: 30px;
                            }
                            p{
                              font: .75em/1em "Arial";
                              font-weight: bold;
                            }
                            span{
                              margin: 0;
                              font: 1em/1.25em "Arial";
                              font-weight: normal;
                              color: #000;
                            }
                          }
                        }
                        .disclamer{
                          margin: 20px 0 30px;
                          font: .75em/1em "Arial";
                          font-weight: bold;
                          a{
                            color: #4f00ff;
                          }
                        }
                        .submit{
                          margin: 0 5px;
                          width: 200px;
                          height: 40px;
                          display: block;
                          font: 1em/40px "Arial";
                          font-weight: bold;
                          color: #fff;
                          text-align: center;
                          text-transform: uppercase;
                          text-decoration: none;
                          background-color: #009fff;
                          border: none;
                          outline: none;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
        .col-sm-12{
          .aside{
            margin: 50px 0 0;
            display: none;
            &__bestHolo,
            &__newHolo,
            &__threeD,
            &__examples,
            &__custom{
              display: none;
            }
          }
        }
      }
    }
  }
}